import logo from './logo.svg';
import './App.css';
import { BrowserRouter as Router, Route, Switch, Redirect } from 'react-router-dom';
import home from './pages/home';

function App() {
  return (
    <div className="App">
     <Router>
        <Switch>
          <Route exact path="/" render={() => <Redirect to="/home" />} />
          <Route path="/home" component={home} />
          
         
        </Switch>
      </Router>
    </div>
  );
}

export default App;
